body {
	box-sizing: border-box;
	margin: 0;
	padding: 0 2px;
}


header {
	display: flex;
	justify-content: center; 
	align-items: center;
	background: black;
	opacity: 0.95;
	color: white;
	font-family: Garamond Georgia Arial;
	font-size: 40px;
	box-sizing: border-box;
	height: 10vh;
}

#content {
	height: 70vh;
	background:  pink;
}

#photo {
	height: 100%;
	margin: 0 auto;
	width: 80vw;
	background: black;
}

img {
	max-width: 100%;
	max-height: 100%;
	width: 100%;
	object-fit: contain;
}

.arrow {
	position: absolute;
	background: black;
	top: 35vh;
	height: 10vh;
	width: 10vh;
	border-radius: 5vh;
}

.arrow.left {
	position: absolute;
	left: 20px;
}

.arrow.left .triangle {
	position: relative;
	right: 2.7vh;
	top: 1vh;
	height: 0;
	width : 0;
	border: 4vh solid transparent;
	border-right: 6vh solid white;
	
}

.arrow.right {
	position: absolute;
	right: 20px;
}

.arrow.right .triangle {
	position: relative;
	left: 2.8vh;
	top: 1vh;
	height: 0;
	width : 0;
	border: 4vh solid transparent;
	border-left: 6vh solid white;
}

input[name=q]{
	background: grey;
	color: white;
}

button[type=submit]{
	background: #ff8d2c;
	color: white;
	padding: 10px;
	border-radius: 0;
}

.gallery-preview {
	height: 20vh;
	background: black;
	display: flex;
	justify-content: center;
	align-items: center;
}

#debug {
	height: 20vh;
	background: pink;
	color: black;
}

